ebkit-search-decoration{-webkit-appearance:none}::-webkit-file-upload-button{-webkit-appearance:button;font:inherit}details,menu{display:block}summary{display:list-item}canvas{display:inline-block}template{display:none}[hidden]{display:none}html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}.srOnly{position:absolute;width:1px;height:1px;margin:-1px;border:0;padding:0;white-space:nowrap;-webkit-clip-path:inset(100%);clip-path:inset(100%);clip:rect(0 0 0 0);overflow:hidden}html{font-size:62.5%}.wrapper{max-width:1200px;width:90%;margin:0 auto}h1,h2,h3,a,p{padding:0;margin:0}h1,h2,h3{font-family:"Roboto",sans-serif;font-size:8rem;text-transform:uppercase}h2{font-size:3rem}h3{font-size:2rem}body{font-family:"Open Sans",sans-serif;color:#272D2D;background-color:#E8E8E8}p{font-size:1.5rem}a{text-decoration:none;font-size:1.5rem;color:#272D2D;transition:0.2s ease-in}a:hover,a:focus{color:#AD2323}img{max-width:100%;min-height:100%;display:block;border-top-left-radius:10px;border-bottom-left-radius:10px;object-fit:cover}.siteName{text-align:center;display:flex;justify-content:center;align-items:center;border-top:5px double black;border-bottom:5px double black;margin-top:20px}.siteName svg{margin-right:50px;font-size:20rem}fieldset{border:none;text-align:center;margin:30px 0}fieldset button{font-size:4rem;border:none;color:black;background-color:#E8E8E8;margin-left:20px}fieldset input,fieldset label{font-family:"Open Sans",sans-serif;font-size:2rem}fieldset input{width:40%;border:none;height:50px;margin-left:10px;padding:5px;border:solid 3px rgba(0,0,0,0);border-radius:15px}fieldset input:focus{border:3px solid green;outline:none}.articleContainer{display:flex;border:5px solid green;border-radius:15px;margin-bottom:20px;box-shadow:5px 5px 5px grey}.articleContainer div{width:50%}.articleContainer .contentBox{padding:10px}.articleContainer .contentBox h2{margin-bottom:5px}.articleContainer .contentBox h3{margin-bottom:10px}.articleContainer .contentBox .published